import React, { Component } from 'react'
import WeeklyScheduleTable from '../Loans/WeeklyScheduleTable'
import LoanService from '../../../services/LoanService'
import TransactionService from '../../../services/TransactionService'
import moment from 'moment'
import CashAvailability from './CashAvailability';

export default class  Dasboard extends Component {
    state = {
        loans: [],
        getLoan: true,
        startDate: moment().startOf('week').toISOString().substring(0, 10),
        endDate: moment().endOf('week').toISOString().substring(0, 10),
        country: 'Todos',
        countries: ['Todos', 'Venezuela', 'Peru'],
      }
      service = new LoanService();
      transactionService = new TransactionService();

      handleChange = (event) => {
        const { name, value } = event.target;
        this.setState({
          [name]: value
        })
      }
    
    
      componentDidMount = () => {
        this.fetchLoans()
        this.fetchAccountsTotals()
      }

      fetchAccountsTotals(){
        if( !this.state.accounts ){
            let country = this.props.userLocation
            console.log(country)
            this.transactionService.getInvestorsAvailability(country)
          .then(response =>{
            this.setState({
              accounts: response
            }) 
          })
          .catch( err =>{
            this.setState({
              accounts:  false
            }) 
          })
        }
      }
    
      fetchLoans(e) {

          if (e) {e.preventDefault(e)}
          const {startDate, endDate} = this.state
          const { userLocation } = this.props
          this.service.getPeriodSchedule(startDate, endDate, userLocation)
          .then( async response => {

            let interestIncome = await response.reduce( (acc, e) => { return acc + e.interest},0)
            let interestPaid = await response.reduce( (acc, e) => { return acc + ((e.interest_pmt) ? e.interest_pmt : 0)},0)  
            let capitalScheduled = await response.reduce( (acc, e) => { return acc + e.principal},0)  
            let capitalRepaid = await response.reduce( (acc, e) => { return acc + ((e.principal_pmt) ? e.principal_pmt : 0)},0)  

            this.setState({
              getLoan: false,
              loans: response,
              loanSummary: {
                  interestIncome,
                  interestPaid,
                  capitalRepaid,
                  capitalScheduled,
              }
            })
    
          })
          .catch(err => {
              this.setState({
                loans: false
            })
          })
      }

    render () {

        const { loans, accounts, loanSummary } = this.state;
        // console.log(loanSummary)
    
        return (
            <div className="content">
                <div>
                    {(accounts && loanSummary) && <CashAvailability details={accounts} summary={loanSummary}/>}
                </div>
                <div>
                    <WeeklyScheduleTable data={loans} />
                </div>
            </div>
        )

    }
    

} 