import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import moment from 'moment'

const CustomTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    minWidth: 700,
  },
  row: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default,
    },
  },
});


function AccTransacionsTable(props) {
  const { classes, data } = props;
  let acc = 0;

  return (
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <CustomTableCell align="right">Fecha de Pago</CustomTableCell>
            <CustomTableCell>Prestatario</CustomTableCell>
            <CustomTableCell align="right">Concepto</CustomTableCell>
            <CustomTableCell align="right">Debito</CustomTableCell>
            <CustomTableCell align="right">Credito</CustomTableCell>
            <CustomTableCell align="right">Total</CustomTableCell>
            <CustomTableCell align="right">Cuenta</CustomTableCell>
            <CustomTableCell align="right">Comentario</CustomTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          { data.map((row, i) => {
            acc = acc + (row.debit - row.credit)
            return ( 
            <TableRow className={classes.row} key={i}>
              <CustomTableCell align="right">{moment(row.date).format('YYYY-MM-DD')}</CustomTableCell>
              <CustomTableCell component="th" scope="row">
                {row._loan ? row._loan._borrower.fullName+" - "+row._loan.capital : "" }
              </CustomTableCell>
              <CustomTableCell align="right">{row.concept}</CustomTableCell>
              <CustomTableCell align="right">{row.debit.toFixed(4)}</CustomTableCell>
              <CustomTableCell align="right">{row.credit.toFixed(4)}</CustomTableCell>
              <CustomTableCell align="right">{acc.toFixed(4)}</CustomTableCell>
              <CustomTableCell align="right">{row.cashAccount}</CustomTableCell>
              <CustomTableCell align="right">{row.comment}</CustomTableCell>
            </TableRow>
          )})}
        </TableBody>
      </Table>
    </Paper>
  );
}

AccTransacionsTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AccTransacionsTable);