import axios from 'axios';
require('dotenv').config();

class ClientService {
  constructor() {
    this.service = axios.create({
      baseURL: `${process.env.REACT_APP_API_URL}/api/test/client`,
      withCredentials: true,
    });
  }
  
  createAccount = (fullName ,gender ,civilStatus ,nationalId ,nationality ,otherNationalities ,placeOfBirth ,DOB ,immigrationCertificate ,academicLevel ,Occupation ,spouseFullName ,spouseNationalId ,spouseDOB ,spousePlaceOfBirth ,street ,residenceName ,residenceNumber ,city ,country ,cellphoneNumber ,email ,employmentStatus ,businessName ,businessAddress ,businessPosition ,startDate ,businessPhoneNumber ,businessEmail ,monthlyIncome ,otherIncome) => {
    return this.service.post('/',
      {
        fullName ,
        gender ,
        civilStatus ,
        nationalId ,nationality ,
        otherNationalities ,
        placeOfBirth ,
        DOB ,
        immigrationCertificate ,
        academicLevel ,
        Occupation ,
        spouseFullName ,
        spouseNationalId ,
        spouseDOB ,
        spousePlaceOfBirth ,
        street ,
        residenceName ,
        residenceNumber ,
        city ,
        country ,
        cellphoneNumber ,
        email ,
        employmentStatus ,businessName ,
        businessAddress ,
        businessPosition ,
        startDate ,
        businessPhoneNumber ,
        businessEmail ,
        monthlyIncome ,
        otherIncome
      })
    .then(response => response.data)
  }

  getClients = (country, fullName) => {
    return this.service.get(`/all-clients/${country}/${fullName}`,)
    .then(response => response.data)
  }

  getClient = (clientId) => {
    return this.service.get(`/detail/${clientId}`,)
    .then(response => response.data)
  }
  
  getInvAccounts = () => {
    return this.service.get('/get-accounts',)
    .then(response => response.data)
  }

}

export default ClientService;