import React, { Component } from 'react';
import './App.scss';
import { Switch, Route } from 'react-router-dom';

// import ProjectList from './components/projects/ProjectList';
import Navbar from './components/navbar/Navbar';
import Footer from './components/footer/Footer';
// import ProjectDetails from './components/projects/ProjectDetails';
import Signup from './components/auth/Signup';
import Login from './components/auth/Login';
import AuthService from './components/auth/AuthService';
import Client from './components/contents/Client/Client'
import ClientList from './components/contents/Client/ClientList'
import Payment from './components/contents/Payment'
import CreateLoan from './components/contents/CreateLoan.jsx'
import CreateInvestor from './components/contents/CreateInvestor'
import NewTransaction from './components/contents/newTransaction'
import DetailInvestor from './components/contents/InvestorDetails/DetailInvestor'
import ClientDetails from './components/contents/Client/ClientDetails'
import LoanList from './components/contents/Loans/LoanList'
import Portfolio from './components/contents/Loans/Portfolio'
import LoanWeekList from './components/contents/Loans/LoanWeekList'
import Dashboard from './components/contents/Dashboard/Dashboard'
import LoanDetails from './components/contents/Loans/Detail/LoanDetails'
import PeriodSchedule from './components/contents/Loans/PeriodSchedule'

class App extends Component {

  constructor(props) {
    super(props)
    this.state = { loggedInUser: null };
    this.service = new AuthService();
  }

  getTheUser = (userObj) => {
    this.setState({
      loggedInUser: userObj
    })
  }

  logout = () => {
    this.service.logout()
      .then(() => {
        this.setState({ loggedInUser: null });
      })
      .then(console.log(this.state))
  }

  fetchUser() {
    if (this.state.loggedInUser === null) {
      this.service.loggedin()
        .then(response => {
          this.setState({
            loggedInUser: response
          })
        })
        .catch(err => {
          this.setState({
            loggedInUser: false
          })
        })
    }
  }

  render() {
    this.fetchUser()


    if (this.state.loggedInUser && this.state.loggedInUser.admin) {
      return (
        <div className="App">
          <header className="App-header">
            <Navbar userInSession={this.state.loggedInUser} logout={this.logout} />
          </header>
          <Switch>
            <Route path='/loan/:loanId' render={ ({match}) => <LoanDetails loanId={match.params.loanId}/>}/>
            <Route path='/client/:clientId' render={ ({match}) => <ClientDetails clientId={match.params.clientId}/>}/>
            <Route path='/create-loan/:clientId' render={ ({match}) => <CreateLoan clientId={match.params.clientId} userLocation={this.state.loggedInUser.location}/>}/>
            <Route exact path='/client' render={() => <Client getUser={this.getTheUser} location={this.state.loggedInUser.location} />} />
            <Route exact path='/payment' render={() => <Payment getUser={this.getTheUser} location={this.state.loggedInUser.location} />} />
            <Route exact path='/create-investor-account' render={() => <CreateInvestor getUser={this.getTheUser} location={this.state.loggedInUser.location} />} />
            <Route exact path='/investor-transaction' render={() => <NewTransaction getUser={this.getTheUser} location={this.state.loggedInUser.location} />} />
            <Route exact path='/investor-details' render={() => <DetailInvestor getUser={this.getTheUser} location={this.state.loggedInUser.location} />} />
            <Route exact path='/loan-list' render={() => <LoanList getUser={this.getTheUser} userLocation={this.state.loggedInUser.location} />} />
            <Route exact path='/portfolio' render={() => <Portfolio getUser={this.getTheUser} userLocation={this.state.loggedInUser.location} />} />
            <Route exact path='/weekly-schedule' render={() => <LoanWeekList getUser={this.getTheUser} location={this.state.loggedInUser.location} />} />
            <Route exact path='/period-schedule' render={() => <PeriodSchedule getUser={this.getTheUser} userLocation={this.state.loggedInUser.location} />} />
            <Route exact path='/client-list' render={() => <ClientList getUser={this.getTheUser} userLocation={this.state.loggedInUser.location} />} />
            <Route exact path='/dashboard' render={() => <Dashboard getUser={this.getTheUser} userLocation={this.state.loggedInUser.location} />} />
          </Switch>
          <Footer />
        </div>
      );
    } else if (this.state.loggedInUser) {
      return (
        <div className="App">
          <header className="App-header">
            <Navbar userInSession={this.state.loggedInUser} logout={this.logout} />
          </header>
          <Footer />
        </div>
      );
    } else {
      return (
        <div className="App">
          <header className="App-header">
            <Navbar userInSession={this.state.loggedInUser} logout={this.logout} />
            <Switch>
              <Route exact path='/signup' render={() => <Signup getUser={this.getTheUser} />} />
              <Route exact path='/login' render={() => <Login getUser={this.getTheUser} />} />
            </Switch>
          </header>
          <Footer />
        </div>
      );
    }
  }
}

export default App;