import React, { Component } from 'react';
import { accounts, fees } from './constants'


class  Payment extends Component {
    constructor(props){
        super(props)
        this.state = {
                date_pmt: new Date().toISOString().substring(0, 10),
                principal_pmt: undefined,
                interest_pmt: undefined,
                paymentId: undefined,
                cashAccount: undefined,
                fee:[]
            }
        
    }

    handleChange = (event) => {
        let  { name, value } = event.target;
        this.setState({ [name]: value });
    }


    handleChangePayment = (event) => {
        let  { name, value } = event.target;
        if (name === "interest_pmt" || "principal_pmt") {
            value = parseFloat(value)
        } 
        this.setState({ [name]: value });
    }

    componentDidMount = () => {
        let { installment } = this.props
        this.setState({
            date_pmt: installment.date,
            principal_pmt: installment.principal,
            interest_pmt: installment.interest,
            paymentId: installment._id,
            cashAccount: undefined,
            fee:[]
        })
    }

    handleChangeFees = (event) => {
        const { value } = event.target;
        let fee = fees[value].fee
        this.setState({ fee: fee });
    }

    render() {
    let { receivePayment, closePaymentOption } = this.props
    return (
        <div className="loan-schedule-holder">
        <div className="loan-payment-content">
            <div className="detail-schedule details-date">
            <label className="acc-title">FECHA</label>
                <input 
                    type="date" 
                    className="form-control"  
                    value={ new Date(this.state.date_pmt).toISOString().substring(0, 10) } 
                    onChange={this.handleChange}
                />
            </div>
            <div className="detail-schedule">
            <label className="acc-title">INTERESES</label>
                <input
                    label="INTERESES"
                    name="interest_pmt"
                    className="form-control input-sm"
                    value={ this.state.interest_pmt }
                    onChange={this.handleChangePayment}
                    type="number"
                />
            </div>
            <div className="detail-schedule">
            <label className="acc-title">CAPITAL</label>
                <input
                    label="principal"
                    name="principal_pmt"
                    type="number"
                    className="form-control input-sm"
                    value={ this.state.principal_pmt }
                    onChange={this.handleChangePayment}
                />  
            </div>
            <div className="detail-schedule">
                <label className="acc-title">CUENTA</label>
                <select className="form-control input-sm" name="cashAccount" id="cashAccount" value={this.state.cashAccount} onChange={e => this.handleChange(e)}>
                        <option></option>
                        {accounts.map((e, i) => <option key={i} value={e}>{e}</option>)}
                </select>
            </div>
            <div className="detail-schedule">
                <label className="acc-title">FEE</label>
                <select className="form-control input-sm" name="feeIndex" id="feeIndex" value={this.state.feeIndex} onChange={e => this.handleChangeFees(e)}>
                        <option></option>
                        {fees.map((e, i) => {
                            return ( <option key={i} value={i} onChange={this.handleChangeFees}>{e.text}</option> )
                        })}
                    </select>
                </div>
            <div className="detail-schedule"> 
            <label className="acc-title">*</label> 
                <div className="payment-operations">    
                <div className='acc-total'>
                    <i className="material-icons" onClick={() => receivePayment(this.state)}>
                        add_box
                    </i>
                </div>
                <div className='acc-total'>
                    <i className="material-icons" onClick={() => closePaymentOption()}>
                        remove_circle
                    </i>
                </div>
                </div>    
            </div>
        </div>
        </div>
            );
        }
}



export default Payment;