import React, { Component } from 'react'
import summaryService from '../../../services/summaryService'

// import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlined';

export default class LoanList extends Component {
    state = {
        getSummary: true
    }
    summaryService = new summaryService();

    componentWillMount() {

        this.fetchSummary()
    }


    fetchSummary() {
        if (this.state.getSummary) {
            let { userLocation } = this.props
            this.summaryService.totals(userLocation)
                .then(response => {
                    console.log(response)
                    this.setState({
                        getSummary: false,
                        data: response
                    })

                })
                .catch(err => {
                    this.setState({
                        data: false
                    })
                })
        }
    }



    render() {

        const { data } = this.state
        return (
            <div className="content">
                {(data) ?
                    data.map( (e) => (
         
                    <div className="loans-summary">
                        <div className="detail-summary">
                            <p className='title'>CARTERA {e[0]._id.toUpperCase()}</p>
                            <p className='total'>${e[0].totalCapitalRemaining.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
                            <p className='acc-title'>AMORTIZADO</p>
                            <p className='acc-total'>${e[0].totalPaid.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
                            <p className='acc-title'>TOTAL NOMINAL</p>
                            <p className='acc-total'>${e[0].totalCapital.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
                        </div>
                        <div className="detail-summary center">
                            <p className='title'>TASA ACTIVA</p>
                            <p className='total'>{(e[0].averageActIntRate * 100).toLocaleString(undefined, {maximumFractionDigits: 2 })}%</p>
                            <p className='acc-title'>TASA</p>
                            <p className='acc-total'>{(e[0].averageIntRate * 100).toLocaleString(undefined, {maximumFractionDigits: 2 })}%</p>
                            <p className='acc-title'>INGRESO POR INTERESES</p>
                            <p className='acc-total'>${(e[0].totalInterest).toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
                        </div>
                        <div className="detail-summary center">
                            <p className='title'>PRESTAMO ACTIVO</p>
                            <p className='total'>${e[0].averageLoanActiveCap.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
                            <p className='acc-title'>PRESTAMO</p>
                            <p className='acc-total'>${e[0].averageLoan.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
                            <p className='acc-title'>TOTAL AMORTIZACION</p>
                            <p className='acc-total'>${e[0].totalAmort.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
                        </div>
                        <div className="detail-summary">
                            <p className='title'>DURACION</p>
                            <p className='total'>{((e[0].totalTimeInDays / 30) / e[0].totalActiveLoans).toLocaleString(undefined, {maximumFractionDigits: 2 })} meses</p>
                            <p className='acc-title'>DURACION INICIAL</p>
                            <p className='acc-total'>{e[0].averageDuration.toLocaleString(undefined, {maximumFractionDigits: 2 })} meses</p>
                            <p className='acc-title'># INVERSIONES</p>
                            <p className='acc-total'>{e[0].totalActiveLoans}</p>
                        </div>
                    </div>))
                    : ""}
            </div>
        )
    }
}