import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const CustomTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    minWidth: 700,
  },
  row: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default,
    },
  },
});


function AccLoanSummaryTable(props) {
  const { classes, loanDetails } = props;

  return (
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <CustomTableCell align="left">Nombre</CustomTableCell>
            <CustomTableCell align="right">Inversin</CustomTableCell>
            <CustomTableCell align="right">%</CustomTableCell>
            <CustomTableCell align="right">Intereses</CustomTableCell>
            <CustomTableCell align="right">Capital</CustomTableCell>
            <CustomTableCell align="right">Ingreso Fee</CustomTableCell>
            <CustomTableCell align="right">Egreso Fee</CustomTableCell>
            <CustomTableCell align="right">Ingreso Total</CustomTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          { loanDetails.map((row, i) => {
            return ( 
            <TableRow className={classes.row} key={i}>
              <CustomTableCell align="left" component="th" scope="row">{row.name} </CustomTableCell>
              <CustomTableCell align="right">{row.investment.toFixed(2)}</CustomTableCell>
              <CustomTableCell align="right">{parseFloat(row.ownership.toFixed(2))*100}%</CustomTableCell>
              <CustomTableCell align="right">{row.interest.toFixed(2)}</CustomTableCell>
              <CustomTableCell align="right">{row.capital.toFixed(2)}</CustomTableCell>
              <CustomTableCell align="right">{row.feeIncome.toFixed(2)}</CustomTableCell>
              <CustomTableCell align="right">{row.feeExpense.toFixed(2)}</CustomTableCell>
              <CustomTableCell align="right">{row.interest + row.feeIncome - row.feeExpense}</CustomTableCell>
            </TableRow>
          )})}
        </TableBody>
      </Table>
    </Paper>
  );
}

AccLoanSummaryTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AccLoanSummaryTable );