import React from 'react';

function InvestorList(props) {
    const { investors } = props;
    return (
        <div className="loan-investor-holder">
            <div className="investor-head ">
                <div className="detail-summary-left">
                    <p className='title'>INVERSIONISTA</p>
                </div>
                <div className="detail-summary">
                    <p className='title'>MONTO</p>
                </div>
                <div className="detail-summary">
                    <p className='title'>%</p>
                </div>
            </div>
            {investors.map((e,i) => {
            return (
                <div key={i} className="investor">
                    <div className="detail-summary-left acc-inv">
                        {e._investor.name.toUpperCase() + " " + e._investor.lastName.toUpperCase()}
                    </div>
                    <div className="detail-summary">
                        <p className="acc-inv">{e.amount}</p>
                    </div>
                    <div className="detail-summary">
                        <p className="acc-inv">{e.pct * 100}</p>
                    </div>
                </div>)
            })}
        </div>
    );
}



export default InvestorList;