import React, { Component } from 'react';
import LoanService from '../../services/LoanService'
import Moment from 'react-moment';

class Payment extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loanId: "",
            accounts: ["PLPERU", "REMPERU", "GCUS", "GFUS"],
            feeDistribution: [{ participants: "P", text: "Patricia 33.33%" }, { participants: "PG", text: "Patricia 50%" }, { participants: "PM", text: "Patricia 30%, Miguel 10%" }, { participants: "GPM", text: "Patricia 45%, Miguel 10%" }, { participants: "M", text: "Miguel 10%" }],
            paymentId: null,
            cashAccount: null,
            interestToPay: 0,
            capitalToPay: 0,
            totalToPay: 0,
            date: null,
            capital: null,
            interest: null,
            loans: [],
            openPayment: false,
            fee: []
        };
        this.service = new LoanService();
    }

    fetchOpenLoans() {
        if (this.state.loans.length === 0) {
            this.service.getOpenLoanDetails()
                .then(response => {

                    if (this.props.location.toLowerCase() === "peru") {
                        response = response.filter(e => {
                            return e._borrower.country.toLowerCase() === "peru"
                        })
                    }

                    this.setState({
                        loans: response
                    })
                })
                .catch(err => {
                    this.setState({
                        loans: false
                    })
                })
        }
    }


    fetchLoans() {
        if (this.state.loans.length === 0) {
            this.service.getLoanDetails()
                .then(response => {
                    this.setState({
                        loans: response
                    })
                })
                .catch(err => {
                    this.setState({
                        loans: false
                    })
                })
        }
    }

    fetchLoanSchedule(e) {
        if (e) {e.preventDefault()}
        let loanId = this.state.loanId
        this.service.getLoanSchedule(loanId)
            .then(response => {
                this.setState({
                    loanSchedule: response
                })
            })
            .catch(err => {
                this.setState({
                    loanSchedule: false
                })
            })
    }

    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState({ [name]: value });
    }


    handleChangeFees = (event) => {
        const { value } = event.target;
        let fee = this.fees[value].fee
        this.setState({ fee: fee });
    }



    openPaymentOption = (event, date, interest, capital, paymentId) => {
        event.preventDefault();
        this.setState({
            openPayment: true,
            date: new Date(date).toISOString().substring(0, 10),
            interestToPay: interest.toFixed(5),
            capitalToPay: capital.toFixed(5),
            paymentId: paymentId
        })
    }

    closePaymentOption = (event) => {
        event.preventDefault();
        this.setState({
            openPayment: false,
            date: null,
            interestToPay: null,
            capitalToPay: null,
            paymentId: null
        })
    }

    fees = [{
        text: "Patricia 33.3%",
        fee: [
            {
                "admin": "5c8103c1cf81366c6c0f1337",
                "fee": 0.333333333
            }
        ]
    }, {
        text: "Patricia 45%, Miguel 10%",
        fee: [
            {
                "admin": "5c8103c1cf81366c6c0f1337",
                "fee": 0.45
            },
            {
                "admin": "5c8103d8cf81366c6c0f1339",
                "fee": 0.1
            }
        ]
    },
    {
        text: "Patricia 30%, Miguel 10%",
        fee: [
            {
                "admin": "5c8103c1cf81366c6c0f1337",
                "fee": 0.3
            },
            {
                "admin": "5c8103d8cf81366c6c0f1339",
                "fee": 0.1
            }
        ]
    },
    {
        text: "Patricia 50%",
        fee: [
            {
                "admin": "5c8103c1cf81366c6c0f1337",
                "fee": 0.5
            }
        ]
    },
    {
        text: "Miguel 10%",
        fee: [
            {
                "admin": "5c80fd19a5b4b86c15c18b8c",
                "fee": 0.1
            }
        ]
    }]

    makePayment = (e, date_pmt, capital_pmt, interest_pmt, paymentId, cashAccount, fee) => {
        this.service.makePayment(date_pmt, capital_pmt, interest_pmt, paymentId, cashAccount, fee)
            .then(response => {
                this.fetchLoanSchedule()
            })
            .then( () => {
                if (this.props.location !== "world") {
                    this.fetchLoans()
                } else {
                    this.fetchOpenLoans()
                }
            })
            .then( () => {
                this.setState({
                    openPayment: false,
                    accounts: ["PLPERU", "REMPERU", "GCUS", "GFUS"],
                    feeDistribution: [{ participants: "P", text: "Patricia 33.33%" }, { participants: "PG", text: "Patricia 50%" }, { participants: "PM", text: "Patricia 30%, Miguel 10%" }, { participants: "GPM", text: "Patricia 45%, Miguel 10%" }, { participants: "M", text: "Miguel 10%" }],
                    paymentId: null,
                    cashAccount: null,
                    date: null,
                    capitalToPay: null,
                    interestToPay: null,
                    fee: [],

                })
            })
            .catch(err => {
                this.setState({
                    loanSchedule: false
                })
            })
    }

    deletePayment = (id) => {
        this.service.deletePayment(id)
            .then(response => {
                this.fetchOpenLoans()
                this.fetchLoanSchedule()
                this.setState({
                    openPayment: false,
                    paymentId: null,
                    cashAccount: null,
                    date: null,
                    capital: null,
                    interest: null,
                    fee: [],
                })
            })
            .catch(err => {
                this.setState({
                    loanSchedule: false
                })
            })

    }

    render() {
        (this.props.location.toLowerCase() === "world") ? this.fetchLoans() : this.fetchOpenLoans()
     
        return (
            <div className="content">
                <h2 className="display-6">Pago de prestamo:</h2>
                <form onSubmit={this.handleFormSubmit}>
                    <div className="form-row input-row-aligner">
                        <div className="form-group col-md-4">
                            <label>Prestamo:</label>
                            <select className="form-control" name="loanId" id="loanId" value={this.state.loanId} onChange={e => this.handleChange(e)}>
                                {(this.state.loans.length === 0) ? <option>loading...</option> : <option>Seleccione un prestamo</option>}
                                {(this.state.loans.length > 0) ? this.state.loans.map(e => <option key={e._id} value={e._id}>{e._borrower.fullName} - {e.capital}</option>) : ""}
                            </select>
                        </div>
                        <div className="form-group col-md-4">
                            <label>Numero de Prestamo:</label>
                            <input className="form-control" type="text" step="any" name="loanId" value={this.state.loanId} disabled />
                        </div>
                        <div className="form-group col-md-2 ">
                            <button className="btn btn-info" onClick={(e) => this.fetchLoanSchedule(e)}>Cronograma de pago</button>
                        </div>

                    </div>
                </form>

                {(this.state.openPayment === true) ?
                    <div>
                        <div className="form-row input-row-aligner">
                            <div className="form-group col-md-4">
                                <label>Intereses a pagar:</label>
                                <input className="form-control" type="number" step="any" name="interestToPay" value={this.state.interestToPay} onChange={e => this.handleChange(e)} />
                            </div>
                            <div className=" form-group col-md-4">
                                <label>Capital a pagar:</label>
                                <input className="form-control" type="number" step="any" name="capitalToPay" value={this.state.capitalToPay} onChange={e => this.handleChange(e)} />
                            </div>
                        </div>
                        <div className="form-row input-row-aligner">
                            <div className="form-group col-md-4" id="account_holder">
                                <label>Cuenta a depositar:</label>
                                <select className="form-control" name="cashAccount" id="cashAccount" value={this.state.cashAccount} onChange={e => this.handleChange(e)}>
                                    <option>Seleccione una cuenta</option>
                                    {this.state.accounts.map(e => <option value={e}>{e}</option>)}
                                </select>
                            </div>
                            <div className="form-group col-md-4">
                                <label>Fecha de pago</label>
                                <input type="date" className="form-control" id="date" name="date" value={this.state.date} onChange={e => this.handleChange(e)} />
                            </div>
                            <div className="form-group col-md-4" id="account_holder">
                                <label>Distribucion de fees:</label>
                                <select className="form-control" name="feeIndex" id="feeIndex" value={this.state.feeIndex} onChange={e => this.handleChangeFees(e)}>
                                    <option>Seleccione una opcin:</option>
                                    {this.fees.map((e, i) => {
                                        return (
                                            <option
                                                value={i}
                                                onChange={this.handleChangeFees}
                                            >
                                                {e.text}
                                            </option>)
                                    })}
                                </select>
                            </div>
                        </div>
                        <button type="button" className="btn btn-success" onClick={(e) => this.makePayment(e, this.state.date, this.state.capitalToPay, this.state.interestToPay, this.state.paymentId, this.state.cashAccount, this.state.fee)} >
                            Pagar
                        </button>
                        <button type="button" className="btn btn-danger" onClick={(e) => this.closePaymentOption(e)}>
                            Cancelar
                        </button>
                    </div>
                    : ""
                }
                {(this.state.loanSchedule) ? (
                    <table className="table table-striped table-hover table-condensed payment-schedule">
                        <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">tracking</th>
                                <th scope="col">Fecha de Pago</th>
                                <th scope="col">Interes</th>
                                <th scope="col">Capital</th>
                                <th scope="col">Cuota</th>
                                <th scope="col">Balance</th>
                                <th scope="col">Pago Capital</th>
                                <th scope="col">Pago Intereses</th>
                                <th scope="col">Pagar</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.loanSchedule.map((e, i) => {
                                return (
                                    <tr key={i}>
                                        <th scope="row">{i}</th>
                                        <td>{e.tracking}</td>
                                        <td><Moment format="YYYY-MM-DD">{e.date}</Moment></td>
                                        <td>{parseFloat(e.interest).toFixed(2)}</td>
                                        <td>{parseFloat(e.principal).toFixed(2)}</td>
                                        <td>{parseFloat(e.payment).toFixed(2)}</td>
                                        <td>{parseFloat(e.balance).toFixed(2)}</td>
                                        {(!isNaN(parseFloat(e.principal_pmt))) ? <td> {parseFloat(e.principal_pmt).toFixed(2)} </td> : <td>0</td>}
                                        {(!isNaN(parseFloat(e.interest_pmt))) ? <td> {parseFloat(e.interest_pmt).toFixed(2)} </td> : <td>0</td>}
                                        <td>{(parseFloat(e.principal_pmt) > 0 || parseFloat(e.interest_pmt) > 0 || parseFloat(i) === 0) ?
                                            (parseFloat(i) !== 0) ? <i class="material-icons" onClick={(event) => this.deletePayment(e._id)}>delete_sweep</i> : ""
                                            :
                                            <i class="material-icons" onClick={(event) => this.openPaymentOption(event, e.date, e.interest, e.principal, e._id)} >add_circle</i>}
                                        </td>
                                    </tr>)
                            })}
                        </tbody>
                    </table>) : ""}
            </div>
        )
    }
}

export default Payment;

