import React, { Component } from 'react';
import TransactionService from '../../../services/TransactionService'
import InvestorService from '../../../services/InvestorService'
// import AccDetailHolder from './AccDetailHolder';
import AccTabsHolder from './AccTabsHolder';
import _ from 'lodash'
// import ExcelFormulas from './helper';
// import moment from 'moment'
class DetailInvestor extends Component {
  constructor(props) {
    super(props);
    this.state = {
      _investor: "",
      totalDeposits: 0,
      totalWithdrawals: 0,
      totalCosts: 0,
      cashAvailable: 0,
      debitTotal: 0,
      totalInvestments: 0,
      creditTotal: 0,
      totalGCUS: 0,
      totalGFUS: 0,
      paidBackCapital: 0,
      interestReceived: 0,
      feeExpenses: 0,
      feeIncome: 0,
      transactions: [],
      cashAccounts: []
    };
    this.TransactionService = new TransactionService();
    this.InvestorService = new InvestorService();
  }

  handleOnClick = async (event) => {
    event.preventDefault();

    const _investor = this.state._investor
    let transactions = await this.TransactionService.getInvestorTransactions(_investor)
    let investments = await this.InvestorService.getInvestorInvestments(_investor)
    let loanDetailts = await this.TransactionService.getLoanInvestorDetails(_investor)

    return Promise.all([transactions, investments, loanDetailts])
      .then(response => {
        this.setState({
          transactions: response[0],
          investments: response[1],
          loanDetails: response[2]
        });
        return response
      })
      .then(response => {
        this.investorDetails(response[0])
      })
      .catch(error => {
        this.setState({
          error: error
        });
      })
  }

  fetchInvestors() {
    if (!this.state.investors) {
      this.InvestorService.getInvestors()
        .then(response => {

          if (this.props.location.toLowerCase() === "peru") {
            response = response.filter(e => {
              return e.location.toLowerCase() === "peru"
            })
          }

          this.setState({
            investors: response
          })
        })
        .catch(err => {
          this.setState({
            error: err
          })
        })
    }
  }

  investorDetails(transactions) {

    let paidBackCapital = transactions.filter((e) => {
      return (e.concept === 'CAPITAL')
    }).reduce((acc, e) => {
      return acc + e.debit
    }, 0)

    let interestReceived = transactions.filter((e) => {
      return (e.concept === 'INTEREST')
    }).reduce((acc, e) => {
      return acc + e.debit
    }, 0)

    let feeExpenses = transactions.filter((e) => {
      return (e.concept === 'FEE')
    }).reduce((acc, e) => {
      return acc + e.credit
    }, 0)

    let feeIncome = transactions.filter((e) => {
      return (e.concept === 'FEE')
    }).reduce((acc, e) => {
      return acc + e.debit
    }, 0)

    let totalDeposits = transactions.filter((e) => {
      return (e.concept === 'DEPOSIT')
    }).reduce((acc, e) => {
      return acc + e.debit
    }, 0)

    let totalWithdrawals = transactions.filter((e) => {
      return (e.concept === 'WITHDRAWAL')
    }).reduce((acc, e) => {
      return acc + e.credit
    }, 0)

    let totalCosts = transactions.filter((e) => {
      return (e.concept === 'COST')
    }).reduce((acc, e) => {
      return acc + e.credit
    }, 0)

    let totalInvestments = transactions.filter((e) => {
      return (e.concept === 'INVESTMENT')
    }).reduce((acc, e) => {
      return acc + e.credit
    }, 0)

    const cashAccountReducer = (transactions) => {
      let accountList = []
      let totals = []

      _.map(_.uniqBy(transactions, 'cashAccount'), _.partial(_.pick, _, ['cashAccount'])).forEach(e => { accountList.push(e.cashAccount) })

      accountList.forEach(e => {
        let total = transactions.filter((j) => { return (j.cashAccount === e) })
          .reduce((acc, k) => { return acc + k.debit - k.credit }, 0)

        let cashAccountTotal = { cashAccount: e, total: total }
        totals.push(cashAccountTotal)
      })
      return totals
    }

    const feesReducer = (transactions) => {
      let accountList = []
      let totals = []

      let fees = transactions.filter((e) => {
        return (e.concept === 'FEE')
      })

      console.log(fees)


      _.map(_.uniqBy(fees, '_investor'), _.partial(_.pick, _, ['_investor'])).forEach(e => { accountList.push(e._investor.name) })
      console.log(accountList)

      accountList.forEach(e => {
        let total = fees.filter((j) => { return (j._investor.name === e) })
          .reduce((acc, k) => { return acc + k.debit - k.credit }, 0)

        let cashAccountTotal = { cashAccount: e, total: total }
        totals.push(cashAccountTotal)
      })
      return totals
    }

    console.log(feesReducer(transactions))

    let totals = cashAccountReducer(transactions)

    let debitTotal = transactions.reduce((acc, e) => {
      return acc + e.debit
    }, 0)
    let creditTotal = transactions.reduce((acc, e) => {
      return acc + e.credit
    }, 0)

    this.setState({
      paidBackCapital: paidBackCapital,
      interestReceived: interestReceived,
      totalInvestments: totalInvestments,
      totalCosts: totalCosts,
      feeExpenses: feeExpenses,
      feeIncome: feeIncome,
      totalDeposits: totalDeposits,
      totalWithdrawals: totalWithdrawals,
      debitTotal: debitTotal,
      creditTotal: creditTotal,
      cashAvailable: debitTotal - creditTotal,
      cashAccounts: totals
    })
  }


  handleChange = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  }




  render() {
    this.fetchInvestors()

    const { cashAvailable, totalDeposits, totalInvestments,
      totalCosts, paidBackCapital, interestReceived, feeExpenses, feeIncome,
      totalWithdrawals, cashAccounts, transactions, investments, loanDetails } = this.state

    return (
      <div className="content">
        <h2 className="display-6">RESUMEN</h2>
        <div className="form-row">
          <div className="form-group col-md-6" id="account_holder">
            <select className="form-control" name="_investor" id="investor" value={this.state._investor} onChange={e => this.handleChange(e)}>
              <option>Seleccionar Inversionista</option>
              {(this.state.investors) ? this.state.investors.map(e => <option key={e._id} value={e._id}>{e.name + " " + e.lastName + " " + e.location}</option>) : ""}
            </select>
          </div>
          <div>
            <button type="submit" className="btn btn-info" onClick={e => this.handleOnClick(e)}>Buscar Inversionista</button>
          </div>
        </div>
        <div className="acc-summary">
          <div className="detail-summary">
            <p className='title'>DISPONIBLE</p>
            <p className='total'>$ {cashAvailable.toLocaleString()}</p>
            {(cashAccounts.length > 0) ? cashAccounts.map((e, i) => {
              return <div><p className='acc-title'>{e.cashAccount}</p><p className='acc-total'>{e.total.toLocaleString()}</p> </div>
            }) : ""}
          </div>
          <div className="detail-summary center">
            <p className='title'>INVERSIONES</p>
            <p className='total'>${(totalInvestments - paidBackCapital).toLocaleString()}</p>
            <p className='acc-title'>INTERESES</p>
            <p className='acc-total'>${interestReceived.toLocaleString()}+</p>
            <p className='acc-title'>REPAGADO</p>
            <p className='acc-total'>${paidBackCapital.toLocaleString()}</p>
            <p className='acc-title'>HISTORICO INVERTIDO</p>
            <p className='acc-total'>${totalInvestments.toLocaleString()}</p>
          </div>
          <div className="detail-summary center">
            <p className='title'>COMISIONES</p>
            <p className='total'>${(feeIncome - feeExpenses).toLocaleString()}</p>
            <p className='acc-title'>INGRESO</p>
            <p className='acc-total'>${feeIncome.toLocaleString()}</p>
            <p className='acc-title'>EGRESO</p>
            <p className='acc-total'>${feeExpenses.toLocaleString()}</p>
          </div>
          <div className="detail-summary">
            <p className='title'>DEPOSITOS</p>
            <p className='total'>${totalDeposits.toLocaleString()}</p>
            <p className='acc-title'>COST</p>
            <p className='acc-total'>${totalCosts.toLocaleString()}</p>
            <p className='acc-title'>RETIROS</p>
            <p className='acc-total'>${totalWithdrawals.toLocaleString()}</p>
          </div>
        </div>
        <div>
          <AccTabsHolder data={transactions} investments={investments} loanDetails={loanDetails} />
        </div>
      </div>
    )
  }
}

export default DetailInvestor;