import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import moment from 'moment'

const CustomTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    minWidth: 700,
  },
  row: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default,
    },
  },
});


function AccInvestmentsTable(props) {
  const { classes, investments } = props;
  
  return (
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <CustomTableCell align="right">Fecha</CustomTableCell>
            <CustomTableCell align="left">Prestamo</CustomTableCell>
            <CustomTableCell>Monto</CustomTableCell>
            <CustomTableCell align="right"> % </CustomTableCell>
            <CustomTableCell align="right">Total invertido</CustomTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          { investments.map((row, i) => {
            return ( 
            <TableRow className={classes.row} key={i}>
              <CustomTableCell align="right">{moment(row._loan.startDate).format('YYYY-MM-DD')}</CustomTableCell>
              <CustomTableCell align="left" component="th" scope="row">
                {row._loan ? row._loan._borrower.fullName+" - "+row._loan.capital : "" }
              </CustomTableCell>
              <CustomTableCell align="right">{parseInt(row.amount / row.pct)}</CustomTableCell>
              <CustomTableCell align="right">{parseFloat(row.pct*100).toFixed(2)+"%"}</CustomTableCell>
              <CustomTableCell align="right">{row.amount.toFixed(2)}</CustomTableCell>
            </TableRow>
          )})}
        </TableBody>
      </Table>
    </Paper>
  );
}

AccInvestmentsTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AccInvestmentsTable);