import React, { Component } from 'react';
import InvestorService from '../../services/InvestorService'

class Client extends Component {
  constructor(props){
    super(props);
    this.state = { 
        name:"",
        lastName:"",
        location:"Peru",
    };
    this.service = new InvestorService();
  }
    
  handleFormSubmit = (event) => {
    event.preventDefault();

    const name = this.state.name
    const lastName = this.state.lastName
    const location = this.state.location


    this.service.createInvestor(name ,lastName ,location)
    .then( response => {
        this.setState({
            name:"",
            lastName:"",
            location:"",
        });
    })
    .catch(error => {
      this.setState({
        error: error
      });
    })
  }

  handleChange = (event) => {  
    const {name, value} = event.target;
    this.setState({[name]: value});
  }
      
  render() {
    return(
      <div class="content">
      <form onSubmit={this.handleFormSubmit}>
          <div class="form-row">
              <div class="form-group col-md-6">
                  <label for="name">Nombre:</label>
                  <input type="text" class="form-control" name="name" value={this.state.name} onChange={e => this.handleChange(e)}/>
              </div>
              <div class="form-group col-md-6">
                  <label for="lastName">Apellido:</label>
                  <input type="text" class="form-control" name="lastName" value={this.state.lastName} onChange={e => this.handleChange(e)}/>
              </div>
              <div class="form-group col-md-6">
                  <label for="location">Region:</label>
                  <select class="form-control" name="location" value={this.state.location} onChange={e => this.handleChange(e)}>
                      <option value="Peru">Peru</option>
                      <option value="USA">USA</option>
                  </select>
              </div>
          </div>
          <button type="submit" class="btn btn-info">Crear Cuenta de Inversin</button>
      </form>
  </div>

    )
  }
}

export default Client;